import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Fade from 'react-reveal/Fade';

class Footer extends Component {
    render() {
        let jhonData = this.props.jhonData;
        return (
            <section className="footer-area">
                <div className="container">
                    <div className="footer-content">
                        <div className="logo wow fadeInDown" data-wow-delay="0.4s">
                            <img src={require('../image/my_logo_1.png')} alt="" loading="lazy" />
                        </div>
                        {/* <Fade top cascade>
                            <ul className="list_style">
                                {
                                    jhonData.socialLinks.map(item => {
                                        return (
                                            <li key={item.name}>
                                                <a href={item.url}><i className={item.className}></i></a>
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                        </Fade> */}
                        <h6>{jhonData.footerMainTitle}<a>{jhonData.footerOwner}</a></h6>
                        <p>{jhonData.footerSubTitle}</p>
                    </div>
                </div>
            </section>
        )
    }
}
export default Footer;