import React, { Component } from "react";
import Navbar from '../myComp/Navbar/Navbar';
import RBanner from '../myComp/Banner/RippleBanner';
import About from '../myComp/About';
import Service from '../myComp/Service';
import Portfolio from '../myComp/Portfolio/Portfolio';
import Testimonial from '../myComp/Testimonial/Testimonial';
import Clientslogo from '../myComp/Clientslogo';
import Blog from '../myComp/Blog/Blog';
import Footer from '../myComp/Footer';
import haigeData from '../content/haigeData';
import haigeDataCh from '../content/haigeDataCh';

class MyHome extends Component {

  render() {
    let langPath = this.props.langPath;
    return (
      <div className="body_wrapper">
        <Navbar jhonData={langPath === "" ? haigeData : haigeDataCh} mainlogo="my_logo_1.png" stickylogo="my_logo_2.png" langPath={langPath} />
        <RBanner jhonData={langPath === "" ? haigeData : haigeDataCh} bClass="banner_shap" imag={window.innerWidth < 1200 ? "derek.png" : 'derek_desktop.png'} textLeft="text-left" />
        <About aClass="about_area" jhonData={langPath != "ch" ? haigeData : haigeDataCh} langPath={langPath} />
        <Blog jhonData={langPath === "" ? haigeData : haigeDataCh} />
        <Service wClass="work_area" jhonData={langPath != "ch" ? haigeData : haigeDataCh} />
        <Testimonial jhonData={langPath === "" ? haigeData : haigeDataCh} />
        <Portfolio jhonData={langPath === "" ? haigeData : haigeDataCh} />
        <Clientslogo jhonData={langPath != "ch" ? haigeData : haigeDataCh} />
        <Footer jhonData={langPath != "ch" ? haigeData : haigeDataCh} />
      </div>
    )
  }
}

export default MyHome;

