import React, { Component } from "react";
import ReactFullpage from "@fullpage/react-fullpage";
import SectionOne from "./SectionOne";
import SectionTwo from "./SectionTwo";
import SectionThree from "./SectionThree";
import SectionFour from "./SectionFour";
import SectionFive from "./SectionFive";
import NavbarThree from "../Navbar/NavbarThree";
import haigeData from '../../content/haigeData';
import haigeDataCh from '../../content/haigeDataCh';

class FullpageSlider extends Component {
  render() {
    var { fullpageProps, langPath } = this.props;
    return (
      <div className="scroll_page">
        <NavbarThree mainlogo="my_logo_1.png" langPath={langPath} />
        <ReactFullpage
          {...fullpageProps}
          render={({ state, fullpageApi }) => {
            console.log("check langPath inside FullpageSlider")
            console.log(langPath)
            return (
              <React.Fragment>
                <SectionOne smallText="Photography" jhonData={langPath === "" ? haigeData : haigeDataCh} />
                <SectionTwo jhonData={langPath === "" ? haigeData : haigeDataCh} />
                <SectionThree jhonData={langPath === "" ? haigeData : haigeDataCh} />
                <SectionFour jhonData={langPath === "" ? haigeData : haigeDataCh} />
                <SectionFive jhonData={langPath === "" ? haigeData : haigeDataCh} />
              </React.Fragment>
            );
          }}
        />
        <div
          className="section_bg"
          style={{
            background: "linear-gradient( 90deg, #f66962 0%, #f78232 100%)",
            position: "absolute",
            height: "100vh",
            width: "100%",
            top: "0",
            left: "0",
            zIndex: "-1",
          }}
        >
          <ul
            data-relative-input="true"
            className="list-unstyled memphis"
            id="scene"
          >
            <li>
              <img srcSet={require("../../image/new-page/line1.png")} alt="" />
            </li>
            <li>
              <img srcSet={require("../../image/new-page/line2.png")} alt="" />
            </li>
            <li>
              <img srcSet={require("../../image/new-page/line3.png")} alt="" />
            </li>
            <li>
              <img srcSet={require("../../image/new-page/topshape2.png")} alt="" />
            </li>
            <li>
              <img
                className="layer layer2"
                data-depth="0.2"
                srcSet={require("../../image/new-page/shape1.png")}
                alt=""
              />
            </li>
            <li>
              <img srcSet={require("../../image/new-page/shape2.png")} alt="" />
            </li>
            <li>
              <img
                srcSet={require("../../image/new-page/bottomshape.png")}
                alt=""
              />
            </li>
            <li>
              <img srcSet={require("../../image/new-page/arrow.png")} alt="" />
            </li>
            <li>
              <img srcSet={require("../../image/new-page/shape3.png")} alt="" />
            </li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
        </div>
      </div>
    )
  }
}

export default FullpageSlider;
