import React, { Component } from 'react';
import Slider from 'react-slick';

class TestimonialSlider extends Component {
  render() {
    let jhonData = this.props.jhonData;

    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      arrows: true,
      autoplay: false,
      pauseOnHover: true,
      autoplaySpeed: 20000,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
      <div>
        <Slider {...settings} className="testimonial_slider">
          <div className="item">
            <div className="author_img">
              <img src={require('../../image/plan_3_compressed.jpg')} alt="" />
            </div>
            <h6>{jhonData.directionManagementMainTitle}</h6>
            <span>{jhonData.directionManagementSubTitle}</span>
            <p>{jhonData.directionManagementContent}</p>
          </div>
          <div className="item">
            <div className="author_img">
              <img src={require('../../image/plan_2_compressed.jpg')} alt="" />
            </div>
            <h6>{jhonData.directionArchitectMainTitle}</h6>
            <span>{jhonData.directionArchitectSubTitle}</span>
            <p>{jhonData.directionArchitectContent}</p>
          </div>
          <div className="item">
            <div className="author_img">
              <img src={require('../../image/plan_1_compressed.jpg')} alt="" />
            </div>
            <h6>{jhonData.directionMarketMainTitle}</h6>
            <span>{jhonData.directionMarketSubTitle}</span>
            <p>{jhonData.directionMarketContent}</p>
          </div>
        </Slider>
      </div>
    );
  }
}
export default TestimonialSlider;