import React, { Component } from "react";
import TestimonialSlider from './TestimonialSlider';
import Sectiontitle from '../../component/Banner/Sectiontitle';
import { Parallax } from 'react-parallax';


class Testimonial extends Component {
  render() {
    let jhonData = this.props.jhonData;
    return (
      <section className="about_area" id="direction">
        <Sectiontitle Title={jhonData.directionMainTitle} TitleP={jhonData.directionSubTitle} />
        <Parallax bgImage={require('../../image/portfolio/img-3_compressed.jpg')} strength={280} className="testimonial_area">
          <div className="container">
            <TestimonialSlider jhonData={jhonData} />
          </div>
        </Parallax>
      </section>
    )
  }
}

export default Testimonial;