import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Fade, Reveal } from 'react-reveal/';

class About extends Component {
    render() {
        let jhonData = this.props.jhonData;
        var { aClass, langPath } = this.props;
        return (
            <section className={`${aClass}`} id="life">
                <div className="container">
                    <div className="row">
                        <div className={langPath === "" ? "col-lg-5 d-flex align-items-center" : "col-lg-4 d-flex align-items-center"}>
                            <Fade bottom cascade duration={1000}>
                                <div className="about_content">
                                    <h2 className="t_color">{jhonData.aboutme}</h2>
                                    <h6>{jhonData.role}</h6>
                                    <p>
                                        {/* {jhonData.aboutdetailsChrono} */}
                                        <br />
                                        {jhonData.aboutdetails}
                                        <br />
                                        {/* {jhonData.aboutdetails2Chrono} */}
                                        <br />
                                        {jhonData.aboutdetails2}
                                        <br />
                                        {/* {jhonData.aboutdetails3Chrono} */}
                                        <br />
                                        {jhonData.aboutdetails3}
                                        <br />
                                        {/* {jhonData.aboutdetails4Chrono} */}
                                        <br />
                                        {jhonData.aboutdetails4}
                                    </p>
                                    <Link to={langPath === "" ? "/life" : "/life/ch"} className="theme_btn active">{jhonData.knowMore}</Link>

                                </div>
                            </Fade>
                        </div>
                        <div className={langPath === "" ? "col-lg-2" : "col-lg-3"} />
                        <div className="col-lg-5">
                            <div className="about_img">
                                <Reveal effect="fadeInRight" duration={1500}><img src={require('../image/life_main_compressed.jpg')} style={{ width: '100%', height: '100%' }} alt="" loading="lazy" /></Reveal>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
export default About;
