import React, { Component } from "react";
import SplitText from "react-pose-text";
const charPoses = {};

class SectionOne extends Component {
  render() {

    let jhonData = this.props.jhonData;

    return (
      <div className="section fp-section fp-tabble section_one">
        <div className="slider_container">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="slider_content slider_content_one">
                  <h6>
                    <span className="br"></span>
                    <SplitText charPoses={charPoses}>{jhonData.knowMoreHomeTownSubTitle}</SplitText>
                  </h6>
                  <h2>
                    <SplitText charPoses={charPoses}>
                      {jhonData.knowMoreHomeTownMainTitle}
                    </SplitText>
                  </h2>
                  <p>
                    {jhonData.knowMoreHomeTownContent}
                  </p>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="slider_image_inner">
                  <span></span>
                  <img
                    src={require("../../image/new-page/life_1_resized.jpg")}
                    alt=""
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SectionOne;
