import React, { Component } from 'react';
import ItemGrid from './ItemGrid';
import Sectiontitle from '../../component/Banner/Sectiontitle';

class portfolio extends Component {
  render() {
    var { pClass } = this.props;
    let jhonData = this.props.jhonData;
    return (
      <section className={`portfolio_area ${pClass}`} id="hobby">
        <div className="container">
          <Sectiontitle Title={jhonData.hobbyMainTitle} TitleP={jhonData.hobbySubTitle} />
          <ItemGrid jhonData={jhonData} />
        </div>
      </section>
    )
  }
}

export default portfolio;
