import React, { Component } from 'react';
import Sectiontitle from '../Banner/Sectiontitle';
import BlogItems from '../Blog/BlogItems';
import Reveal from 'react-reveal/Reveal/';
class Blog extends Component {
    render() {
        let jhonData = this.props.jhonData;
        return (
            <section className="blog_area" id="status">
                <div className="container">
                    <Sectiontitle Title={jhonData.statusMainTitle} TitleP={jhonData.statusSubTitle} />
                    <Reveal effect="fadeInUp" duration={1000}>
                        <div className="row">
                            <BlogItems bTitle={jhonData.statusWorkTitle} bDetails={jhonData.statusWorkContent} image="social_1_compressed.jpg" Pdata={jhonData.statusWorkPublish} Tdata={jhonData.statusWorkTag} />
                            <BlogItems bTitle={jhonData.statusExerciseTitle} bDetails={jhonData.statusExerciseContent} btnText="Read More" image="social_3_compressed.jpg" Pdata={jhonData.statusExercisePublish} Tdata={jhonData.statusExerciseTag} />
                            <BlogItems bTitle={jhonData.statusSocialTitle} bDetails={jhonData.statusSocialContent} btnText="Read More" image="social_2_compressed.jpg" Pdata={jhonData.statusSocialPublish} Tdata={jhonData.statusSocialTag} />
                        </div>
                    </Reveal>
                </div>
            </section>
        )
    }
}
export default Blog;