import React, { Component } from "react";
import FullpageSlider from "../myComp/Creativecarousel/FullpageSlider";
import fullpageOptions from "../myComp/Creativecarousel/fullpageConfig";
import fullpageOptionsCh from "../myComp/Creativecarousel/fullpageConfigCh";

class MyLife extends Component {
  render() {
    let langPath = this.props.langPath;
    return (
      <React.Fragment>
        <FullpageSlider
          // isFooterOpen={this.state.isFooterOpen}
          // hide={this.hide}
          // show={this.show}
          // {...fullpageOptions}
          fullpageProps={langPath === "" ? fullpageOptions : fullpageOptionsCh}
          langPath={langPath}
        />
      </React.Fragment>
    )
  }
}

export default MyLife;

