import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { NotFound } from "./myPage/My404";
import MyHome from "./myPage/MyHome";
import MyLife from "./myPage/MyLife";

class App extends Component {
  render() {
    return (
      <React.Fragment>
        <Router>
          <Switch>
            <Route exact path="/" component={() => (<MyHome langPath="" />)} />
            <Route exact path="/ch" component={() => (<MyHome langPath="ch" />)} />
            <Route exact path="/life" component={() => (<MyLife langPath="" />)} />
            <Route exact path="/life/ch" component={() => (<MyLife langPath="ch" />)} />
            <Route component={NotFound} />
          </Switch>
        </Router>
      </React.Fragment>
    );
  }
}

export default App;
