import React, { Component } from "react";
import Sticky from "react-stickynode";
import { Link } from "react-scroll";
import { Link as RouterLink } from 'react-router-dom';

class Navbar extends Component {

  render() {
    var { mClass, mContainer, mainlogo, stickylogo, jhonData, langPath } = this.props;

    return (
      <Sticky top={0} innerZ={9999} activeClass="navbar_fixed">
        <nav className={`navbar navbar-expand-lg navbar-light ${mClass}`}>
          <div className={`container ${mContainer}`}>
            <a className="navbar-brand logo_h">
              <img src={require("../../image/" + mainlogo)} alt="" />
              <img src={require("../../image/" + stickylogo)} alt="" />
            </a>
            <RouterLink to={langPath === "" ? "./ch" : "./"} className="btn get-btn get-btn-two d-lg-none d-md-block login" onClick={() => { window.scrollTo({ top: 0, behavior: 'smooth' }) }}>
              {langPath === "" ? '点击中文版' : 'Click English Ver'}
            </RouterLink>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
            </button>
            <div
              className="collapse navbar-collapse offset"
              id="navbarSupportedContent"
            >
              <ul className="nav navbar-nav m-auto">
                <li className="nav-item">
                  <Link
                    className="nav-link"
                    activeClass="active"
                    to="about"
                    spy={true}
                    smooth={true}
                    offset={-86}
                    duration={500}
                  >
                    {jhonData.navAbout}
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link"
                    activeClass="active"
                    to="life"
                    spy={true}
                    smooth={true}
                    offset={-86}
                    duration={500}
                  >
                    {jhonData.navLife}
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link"
                    activeClass="active"
                    to="status"
                    spy={true}
                    smooth={true}
                    offset={-86}
                    duration={500}
                  >
                    {jhonData.navStatus}
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link"
                    activeClass="active"
                    to="specialty"
                    spy={true}
                    smooth={true}
                    offset={-86}
                    duration={500}
                  >
                    {jhonData.navSpecialty}
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link"
                    activeClass="active"
                    to="direction"
                    spy={true}
                    smooth={true}
                    offset={-86}
                    duration={500}
                  >
                    {jhonData.navDirection}
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link"
                    activeClass="active"
                    to="hobby"
                    spy={true}
                    smooth={true}
                    offset={-86}
                    duration={500}
                  >
                    {jhonData.navHobby}
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link"
                    activeClass="active"
                    to="contact"
                    spy={true}
                    smooth={true}
                    offset={-86}
                    duration={500}
                  >
                    {jhonData.navContact}
                  </Link>
                </li>
              </ul>
              <ul className="nav navbar-nav navbar-right d-md-none d-lg-block">
                <li className="nav-item">
                  <RouterLink to={langPath === "" ? "./ch" : "./"} className="login" onClick={() => { window.scrollTo({ top: 0, behavior: 'smooth' }) }}>
                    {langPath === "" ? '点击中文版' : 'Click English Ver'}
                  </RouterLink>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </Sticky>
    );
  }
}

export default Navbar;
