import React, { Component } from 'react';
import Clientsliders from 'react-slick';
import Sectiontitle from '../component/Banner/Sectiontitle';
import Reveal from "react-reveal/Reveal/";
import Fade from 'react-reveal/Fade';
import { Link } from 'react-router-dom';

class Clientslogo extends Component {
    render() {
        let jhonData = this.props.jhonData;
        const settings = {
            dots: false,
            infinite: true,
            speed: 500,
            arrows: false,
            slidesToShow: 6,
            slidesToScroll: 1,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 4,
                        slidesToScroll: 3,
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 2,
                        initialSlide: 2
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        };
        return (
            <section className="clients_logo_area" id="contact">
                <div className="container">
                    <Sectiontitle Title={jhonData.contactMainTitle} TitleP={jhonData.contactSubTitle} />

                    <div className="container">
                        <div className="footer-content">
                            <Fade top cascade>
                                <ul className="list_style">
                                    {
                                        jhonData.socialLinks.map(item => {
                                            return (
                                                <li key={item.name}>
                                                    <a><i className={item.className}></i></a>
                                                </li>
                                            )
                                        })
                                    }
                                </ul>
                            </Fade>
                        </div>
                    </div>

                    <div className="section fp-section fp-tabble section_one contact-area">
                        <div>
                            <div className="container">
                                <div className="row row-reverse">
                                    {jhonData.contact &&
                                        jhonData.contact.map((item) => {
                                            return (
                                                <React.Fragment key={item.id}>
                                                    <div className="col-md-5 contact-content-center">
                                                        <Reveal effect="fadeInLeft" duration={500}>
                                                            <div className="contact_info">
                                                                <ul className="nav">
                                                                    {item.menuItems.map((info) => {
                                                                        return (
                                                                            <li className="item" key={info.id}>
                                                                                <div className="media">
                                                                                    <i className={info.icon}></i>
                                                                                    <div className="media-body">
                                                                                        {info.text}
                                                                                    </div>
                                                                                </div>
                                                                            </li>
                                                                        );
                                                                    })}
                                                                </ul>
                                                            </div>
                                                        </Reveal>
                                                    </div>
                                                </React.Fragment>
                                            );
                                        })}
                                </div>
                            </div>
                        </div>
                    </div>
                    <Clientsliders {...settings} className="clients_slider">
                        <div className="item">
                            <a><img src={require('../image/6.png')} alt="" loading="lazy" /></a>
                        </div>
                        <div className="item">
                            <a><img src={require('../image/1.png')} alt="" loading="lazy" /></a>
                        </div>
                        <div className="item">
                            <a><img src={require('../image/5.png')} alt="" loading="lazy" /></a>
                        </div>
                        <div className="item">
                            <a><img src={require('../image/2.png')} alt="" loading="lazy" /></a>
                        </div>
                        <div className="item">
                            <a><img src={require('../image/3.png')} alt="" loading="lazy" /></a>
                        </div>
                        <div className="item">
                            <a><img src={require('../image/4.png')} alt="" loading="lazy" /></a>
                        </div>
                        <div className="item">
                            <a><img src={require('../image/6.png')} alt="" loading="lazy" /></a>
                        </div>
                    </Clientsliders>
                </div>
            </section>
        );
    }

}
export default Clientslogo;