import React, { Component } from "react";
import SplitText from "react-pose-text";

const charPoses = {};

class SectionTwo extends Component {
  render() {

    let johnData = this.props.jhonData;

    return (
      <div className="section fp-section fp-tabble">
        <div className="slider_container">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="slider_content slider_content_two">
                  <h6>
                    <span className="br"></span>
                    <SplitText charPoses={charPoses}>{johnData.knowMoreTeenageSubTitle}</SplitText>
                  </h6>
                  <h2>
                    <SplitText charPoses={charPoses}>{johnData.knowMoreTeenageMainTitle}</SplitText>
                  </h2>
                  <p>
                    {johnData.knowMoreTeenageContent}
                  </p>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="slider_image_inner">
                  <span></span>
                  <img
                    src={require("../../image/new-page/life_2_resized.jpg")}
                    alt=""
                    loading="lazy"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SectionTwo;
